.host {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding: 0 10px 0px 10px;
}

.leftColumn {
    position: fixed;
    left: 0;
    top: 20px;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 25%;
    min-width: 150px;
    overflow: hidden;
}

.leftColumnHeader {
    display: flex;
    flex: 0;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.backButtonContainer {
    align-self: flex-start;
    margin-top: 18px;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    margin-left: 30px;
    width: 25px;
    height: 25px;
}

.backIco {
    width: 24px;
    height: 24px;
}

.title {
    text-align: center;
    font-family: var(--app-text-main-font);
    font-size: 2em;
    font-weight: bold;
    color: var(--app-color-gray-dark);
    width: 100%;
    word-break: break-word;
    text-transform: capitalize;
}

.titleContainer {
    display: flex;
    justify-content: flex-start;
    flex: 1;

    p {
        margin-left: 10px;
        font-family: var(--app-text-main-font);
        font-size: 1.2em;
        line-height: 1.2em;
        text-align: left;
        color: var(--app-color-gray-dark);
        word-break: break-all;
    }
}

.componentControlsContainer {
    margin-bottom: 25%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: flex-start;
}

.componentControlsContainerText {
    margin: 0 0 0 34px;
    cursor: pointer;
}

.componentControlsText {
    text-align: left;
    color: var(--app-color-gray-dark);
    // white-space: pre;
    word-break: break-all;
    text-overflow: ellipsis;
}

.componentControlsIcon {
    margin-bottom: -5px;
    margin-right: 10px;
    color: var(--app-color-gray-dark);
    width: 22px;
    height: 22px;
}

.duplicateButtonContainer {
    margin-bottom: 30%;

    p {
        margin: 0 0 0 54px;
        text-align: left;
        color: var(--app-color-gray-dark);
        cursor: pointer;
    }

    .deleteIco {
        margin-bottom: -5px;
        margin-right: 10px;
        color: var(--app-color-gray-dark);
        width: 22px;
        height: 22px;
    }
}

.deleteButtonContainer {
    p {
        margin: 0 0 0 54px;
        text-align: left;
        color: var(--app-color-gray-dark);
        cursor: pointer;
    }

    .deleteIco {
        margin-bottom: -5px;
        margin-right: 10px;
    }
}

